<template>
    <meta name="description" content="NFT Card Gallery - Buy and Sell NFTs">
    <div class="container-fluid p-3">
        <div class="row">
            <div class="col" v-for="card in cards" :key="card.id">
                <div class="card border-primary m-3" style="min-width: 20rem">
                    <div class="card-body">
                        <img :src="card.image" class="card-img-top" :alt="'Picture of ' + card.title" />
                        <div class="card-header lead mt-3" aria-label="NFT Title">{{ card.title }}</div>
                        <div class="card-body">
                            <p>{{ card.description }}</p>
                            <div class="d-flex justify-content-between align-items-center">
                                <button class="btn btn-primary" @click="showAlert" aria-label="Buy">Buy</button>
                                <p class="lead" role="text" aria-label="NFT Price">
                                    {{ card.price }} $
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            cards: [],
        };
    },
    mounted() {
        this.loadJsonData();
    },
    methods: {
        async loadJsonData() {
            try {
                const response = await fetch("fakeapi/nft/nfts.json");
                const jsonData = await response.json();
                this.cards = jsonData.map((item) => ({
                    id: item.id,
                    title: item.title,
                    description: item.description,
                    image: item.image,
                    price: item.price,
                }));
            } catch (error) {
                console.error("Error loading JSON data:", error);
            }
        },
        showAlert() {
            alert("Buying is not implemented :)");
        },
    },
};
</script>
